.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  body {
    background-color: blueviolet;
  }
  
  .box {
    border: 3px solid darkblue;
    width: 300px;
    height: 300px;
    background-color: darkorange;
    position: absolute;
    top: 35%;
    left: 45%;
    text-align: center;
    justify-content: center;
  }
  h1 {
    text-align: center;
    justify-content: center;
    position: absolute;
    top: 37%;
    left: 30%;
  }
  
  button {
    padding: 10px;
    font-size: 15px;
    position: absolute;
    top: 62%;
    left: 36%;
    border-radius: 15px;
    cursor: pointer;
  }
  button:hover {
    background-color: deeppink;
  }
  